import React, { useEffect } from 'react';
import { convertFromRaw, convertToRaw, Editor, EditorState, RichUtils } from 'draft-js';
import 'draft-js/dist/Draft.css';
import './Note.scss';

export const Note = ({ itemId }) => {
  useEffect(() => {
    let modifiedItemId;
    if(itemId.startsWith("ROBIN_")){
      modifiedItemId=itemId;
    }
    else{
      modifiedItemId=`${"ROBIN_"}${itemId}`
    }
    const savedItem = localStorage.getItem(modifiedItemId);
    if (savedItem) {
      const rawContentFromStore = convertFromRaw(JSON.parse(savedItem));
      const editorStateLocal = EditorState.createWithContent(rawContentFromStore);
      setEditorState(editorStateLocal);
    } else {
      setEditorState(() => EditorState.createEmpty());
    }
  }, []);

  const modifyState = (state) => {
    setEditorState(state);
    const contentRaw = convertToRaw(editorState.getCurrentContent());
    localStorage.setItem('ROBIN_' + itemId, JSON.stringify(contentRaw));
  };

  const [editorState, setEditorState] = React.useState(() => EditorState.createEmpty());

  const handleKeyCommand = (command) => {
    const newState = RichUtils.handleKeyCommand(editorState, command);
    if (newState) {
      return modifyState(newState);
    }
    return 'not-handled';
  };

  return (
    <div className="editor">
      <Editor
        style={{heigth: "100%"}}
        placeholder="Enter note here"
        editorState={editorState}
        onChange={modifyState}
        handleKeyCommand={handleKeyCommand}
      />
    </div>
  );
};
