import { TextField } from "@adobe/react-spectrum";

import "./NewFile.scss";
import React, { useState } from "react";

const NewField = ({ add, placeholder }) => {
  const [term, setTerm] = useState("");

  const addTerm = () => {
    if (term) {
      add(term);
      setTerm("");
    }
  };

  return (
    <div className="new-wrapper d-flex pb-10 pl-10">
      <TextField
        aria-label="Enter URL"
        placeholder={placeholder}
        value={term}
        onKeyDown={(e) => {
          if (e.keyCode === 13) {
            addTerm();
          }
        }}
        onChange={(e) => setTerm(e)}
      />
      <button
        onClick={() => {
          addTerm();
        }}
        className="add-btn"
      >
        Add
      </button>
    </div>
  );
};

export default NewField;
