import React, { useEffect, useState } from 'react';
import { Link } from '../bookmark-link/Link';
import { BACKEND_URL } from '../../constants';

const RSSFirst = ({ link }) => {
  const [links, setLinks] = useState([]);
  const [localState, setLocalState] = useState([]);
  useEffect(() => {
    fetchBookmarks();
  }, []);

  useEffect(() => {
    const savedItem = localStorage.getItem('ROBIN_LINK_CACHES') || '{}';
    setLocalState(JSON.parse(savedItem));
  }, []);

  const setLinkState = (link, response) => {
    const localState = localStorage.getItem('ROBIN_LINK_CACHES') || '{}';
    let modifiedState = { ...JSON.parse(localState) };
    modifiedState = { ...modifiedState, [link]: response };
    localStorage.setItem('ROBIN_LINK_CACHES', JSON.stringify(modifiedState));
    setLinks(response);
  };

  const checkInState = (link) => {
    const localState = JSON.parse(localStorage.getItem('ROBIN_LINK_CACHES') || '{}');
    if (localState[link]) {
      const modifiedState = [...links, localState[link][0]];
      setLinks(modifiedState);
      return true;
    }
    return false;
  };

  const fetchBookmarks = () => {
    const headers = new Headers();
    headers.append('Content-Type', 'application/json');

    if (checkInState(link)) {
      return;
    }
    const raw = JSON.stringify([link]);

    const requestOptions = {
      method: 'POST',
      headers: headers,
      body: raw,
    };

    fetch(`${BACKEND_URL}/meta`, requestOptions)
      .then((response) => response.json())
      .then((result) => setLinkState(link, result.data))
      .catch((error) => console.log('error', error));
    return () => {};
  };

  return (
    <div className="bookmark-list">
      {links.map((link, index) => (
        <Link {...link} key={'LINK_' + index} />
      ))}
    </div>
  );
};

export default RSSFirst;
