import React, { useEffect, useState } from 'react';
import './Link.scss';

import { faCheck, faExclamation, faHandRock, faTrash } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export const Link = ({
  image,
  title,
  description,
  url,
  index,
  deleteLink,
  showDeleteIcon = false,
}) => {
  const [isShown, setShown] = useState(false);
  console.log({isShown,showDeleteIcon})
  return (
    <div className="link-box-wrapper d-flex justify-between" onMouseEnter={() => setShown(true)} onMouseLeave={() => setShown(false)}>
      <a
        className="link-box fl-90"
        href={url}
        target="_blank"
      >
        <div className="icon">
          <img src={image} />
        </div>
        <div className="">
          <div className="title">{title}</div>

          <div className="sub-title">{description}</div>
        </div>
      </a>
      <div onMouseEnter={() => setShown(true)} onMouseLeave={() => setShown(false)}>
        {showDeleteIcon && isShown && (
          <div className="action-item">
            <FontAwesomeIcon
              icon={faTrash}
              className="fa-icon pointer"
              onClick={(e) => {
                e.preventDefault();
                e.stopPropagation()
                deleteLink(index)}}
            />
          </div>
        )}
      </div>
    </div>
  );
};
