/* eslint-disable no-eval */

import React, { useEffect, useState, useCallback } from 'react';
import { Link } from '../bookmark-link/Link';
import './Bookmark.scss';
import NewField from '../add-new/NewField';
import { BACKEND_URL } from '../../constants';
import { List as ListItems, arrayMove } from 'react-movable';
import Mover from '../mover.svg';

const initialUrls = ['https://www.robingood.com'];

const STATE_TYPES = {
  LINK: 'link',
  URLS: 'urls',
  VISITED_URLS: 'vurls',
};

const Bookmark = ({ itemId, loadDefault = false }) => {
  const [links, setLinks] = useState([]);
  const [urls, setUrls] = useState([]);
  const [visitedUrls, setVisitedUrls] = useState([]);

  useEffect(() => {
    fetchBookmarks();
  }, [ urls]);

  useEffect(() => {
    console.log({ loadDefault });
    const shouldUseDefault = !!localStorage.getItem('LOAD_DEFAULT');
    if (shouldUseDefault) {
      setUrls(initialUrls);
      localStorage.setItem('LOAD_DEFAULT', false);
      return;
    }

    const storedLink = 'ROBIN_' + itemId + '_LINK';
    const storedUrls = 'ROBIN_' + itemId + '_URLS';
    const storedVisitedUrls = 'ROBIN_' + itemId + '_VURLS';
    const savedLinks = localStorage.getItem(storedLink) || '[]';
    const savedUrls = localStorage.getItem(storedUrls) || '[]';
    const savedVisitedUrls = localStorage.getItem(storedVisitedUrls) || '[]';
    if (savedLinks) {
      setLinks(JSON.parse(savedLinks));
      setUrls(JSON.parse(savedUrls));
      setVisitedUrls(JSON.parse(savedVisitedUrls));
    }
  }, [itemId, loadDefault]);

  const modifyState = useCallback((type, urls) => {
    switch (type) {
      case STATE_TYPES.LINK: {
        const key = 'ROBIN_' + itemId + '_LINK';
        localStorage.setItem(key, JSON.stringify(urls));
        setLinks(urls);
        break;
      }
      case STATE_TYPES.URLS: {
        const key = 'ROBIN_' + itemId + '_URLS';
        localStorage.setItem(key, JSON.stringify(urls));
        setUrls(urls);
        break;
      }
      case STATE_TYPES.VISITED_URLS: {
        const key = 'ROBIN_' + itemId + '_VURLS';
        setVisitedUrls(urls);
        localStorage.setItem(key, JSON.stringify(urls));
        break;
      }
      default:
        return;
    }
  });

  const fetchBookmarks = () => {
    var headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let filteredLinks;
    if (visitedUrls.length === 0) {
      filteredLinks = urls;
    } else {
      filteredLinks = urls.filter((item) => visitedUrls.indexOf(item) === -1);
    }

    var raw = JSON.stringify([...filteredLinks]);
    var requestOptions = {
      method: 'POST',
      headers: headers,
      body: raw,
    };
    let linksArray = [];

    fetch(`${BACKEND_URL}/meta`, requestOptions)
      .then((response) => response.json())
      .then((result) => {
        if (result.data) {
          result.data.forEach((link) => {
            if (linksArray.indexOf(link.incomingUrl) === -1) {
              linksArray.push(link.incomingUrl);
            }
          });

          modifyState(STATE_TYPES.VISITED_URLS, [...visitedUrls, ...linksArray]);
          // setVisitedUrls([...visitedUrls, ...linksArray]);

          const previousLinks = [...links];
          modifyState(STATE_TYPES.LINK, [...previousLinks, ...result.data]);

          // setLinks([...previousLinks, ...result.data]);
        }
      })
      .catch((error) => console.log('error', error));
  };

  const add = (value) => {
    modifyState(STATE_TYPES.URLS, [...urls, value]);

    // setUrls([...urls, value]);
  };

  const deleteLink = (index) => {
    console.log("Link Deleting");
    console.log(index);
    let linkCopy = [...links];
    linkCopy.splice(index, 1);
    modifyState(STATE_TYPES.LINK, linkCopy);

    // setLinks(linkCopy);
  };

  return (
    <>
      <div className="bookmark-wrapper">
        <NewField add={add} placeholder={'Enter URL'} />

        <div className="bookmark-list">
          <ListItems
            lockVertically
            values={links}
            onChange={({ oldIndex, newIndex }) =>
              modifyState(STATE_TYPES.LINK, arrayMove(links, oldIndex, newIndex))
            }
            renderList={({ children, props }) => <ul {...props}>{children}</ul>}
            renderItem={({ value, props }) => {
              const index = props.key;
              const extraParams={showDeleteIcon:true,index,...value,deleteLink}
              return (
                <li {...props} className="input-list-item">
                  <div className="pointer handle" data-movable-handle>
                    <img src={Mover} alt="React Logo" className="move-handler" />
                  </div>
                   <Link {...extraParams} key={'LINK_' + index}></Link>
                </li>
              );
            }}
          />
        </div>
      </div>
    </>
  );
};

export default Bookmark;
