import React, { useEffect, useState } from 'react';
import ResponsiveGridLayout from 'react-grid-layout';

import Card from './components/card/Card';
import { Note } from './components/note/Note';
import { Header, HeaderComponent, Heading } from './components/heading/heading';
import { List } from './components/list/List';
import Bookmark from './components/bookmarks/Bookmark';
import RSS from './components/rss-feed/RSS';
import './App.scss';
import '../node_modules/react-grid-layout/css/styles.css';
import '../node_modules/react-resizable/css/styles.css';
import { lightTheme, Provider } from '@adobe/react-spectrum';
import * as faker from 'faker';
import 'react-tippy/dist/tippy.css';
import MainIcon from './components/mainicon.svg';

import robin from './assets/robin-icon.png'
import patreon from './assets/patreon.jpeg'
import Footer from './components/footer/footer';



const ITEM_TYPES = {
  NOTES: 'notes',
  RSS: 'rss',
  TODO: 'todo',
  BOOKMARK: 'bookmark',
};

const App = () => {
  const initialState = [
    {
      key: 'a',
      x: 0,
      y: 0,
      w: 4,
      h: 13,
      minW: 4,
      minH: 4,
      heading: 'Notes',
      itemType: ITEM_TYPES.NOTES,
      itemId: '1',
      itemData: '',
    },
    {
      key: 'b',
      x: 4,
      y: 0,
      w: 4,
      h: 13,
      minW: 4,
      minH: 4,
      heading: 'List',
      itemType: ITEM_TYPES.TODO,
      itemId: '2',
      itemData: '',
    },
    {
      key: 'c',
      x: 8,
      y: 0,
      w: 4,
      h: 13,
      minW: 4,
      minH: 4,
      heading: 'Links',
      itemType: ITEM_TYPES.BOOKMARK,
      loadDefault: false,
      itemId: '3',
      itemData: '',
    },
    {
      key: 'd',
      x: 12,
      y: 0,
      w: 4,
      h: 13,
      minW: 4,
      minH: 4,
      heading: 'Robin Good TRUST-able',
      itemType: ITEM_TYPES.RSS,
      loadDefault: false,
      itemId: '4',
      itemData: '',
      url: 'https://robingood.substack.com/feed '
    }
  ];

  const newRSSItem = {
    y: 0,
    w: 4,
    h: 13,
    minW: 4,
    minH: 4,
    heading: 'RSS Feed',
    itemType: ITEM_TYPES.RSS,
    itemData: '',
    loadDefault: false,
  };

  const newNoteItem = {
    y: 0,
    w: 4,
    h: 13,
    minW: 4,
    minH: 4,
    heading: 'Notes',
    itemType: ITEM_TYPES.NOTES,
    itemData: '',
  };

  const newTodoItem = {
    y: 0,
    w: 4,
    h: 13,
    minW: 4,
    minH: 4,
    heading: 'List',
    itemType: ITEM_TYPES.TODO,
    itemData: '',
  };

  const newBookmarkItem = {
    y: 0,
    w: 4,
    h: 13,
    minW: 4,
    minH: 4,
    heading: 'Links',
    itemType: ITEM_TYPES.BOOKMARK,
    itemData: '',
  };

  const newItemX = () => {
    let length = state.length - 1;

    if (!state[length] || state[length].x === 12) {
      return 0;
    } else return state[length].x + 4;
  };

  const [showAddDialog, setShowAddDialog] = useState(false);
  const [state, setState] = useState([]);

  const renderItem = (type, Id, url, loadDefault) => {
    switch (type) {
      case ITEM_TYPES.RSS: {
        return <RSS itemId={Id} url={url} />;
        break;
      }
      case ITEM_TYPES.TODO: {
        return <List itemId={Id} />;
        break;
      }
      case ITEM_TYPES.NOTES: {
        return <Note itemId={Id} />;
        break;
      }
      case ITEM_TYPES.BOOKMARK: {
        return <Bookmark itemId={Id} loadDefault={loadDefault} />;
        break;
      }
    }
  };

  const hasBodyPadding = (item) => {

    switch (item) {
      case ITEM_TYPES.RSS:
      case ITEM_TYPES.TODO:
        case ITEM_TYPES.BOOKMARK:
        return false;

      case ITEM_TYPES.NOTES: {
        return true;
      }
    }
  };

  const addNewItem = (item, title, url) => {
    const newX = newItemX();
    const itemId = faker.random.uuid();
    let newItem;
    switch (item) {
      case ITEM_TYPES.RSS: {
        newItem = { ...newRSSItem, x: newX, key: itemId, itemId, url, heading: title };
        break;
      }
      case ITEM_TYPES.TODO: {
        newItem = { ...newTodoItem, x: newX, key: itemId, itemId };
        break;
      }
      case ITEM_TYPES.NOTES: {
        newItem = { ...newNoteItem, x: newX, key: itemId, itemId };
        break;
      }
      case ITEM_TYPES.BOOKMARK: {
        newItem = { ...newBookmarkItem, x: newX, key: itemId, itemId, loadDefault: false };
      }
    }

    const newState = [...state];
    newState.push(newItem);
    localStorage.setItem('ROBINGOOD_NEW_TAB', JSON.stringify(newState));
    setState(newState);
  };

  const modifyOrientation = (layouts) => {
    const modifiedState = [...state];
    layouts.forEach((layout, index) => {
      const orientation = {
        x: layout.x,
        y: layout.y,
        w: layout.w,
        h: layout.h,
        minW: layout.minW,
        minH: layout.minH,
      };
      const newObject = { ...modifiedState[index], ...orientation };
      modifiedState[index] = newObject;
    });
    localStorage.setItem('ROBINGOOD_NEW_TAB', JSON.stringify(modifiedState));
    console.log({ modifiedState });
    setState(modifiedState);
  };

  useEffect(() => {
    const currentState = localStorage.getItem('ROBINGOOD_NEW_TAB');
    if (currentState) {
      setState(JSON.parse(currentState));
    } else {
      setState(initialState);
    }
  }, []);

  const deleteItem = (id) => {
    const modifiedState = [...state];
    console.log({ id });
    const index = modifiedState.findIndex((item) => item.itemId === id);
    console.log({ modifiedState });
    console.log({ index });
    if (index > -1) {
      modifiedState.splice(index, 1);
    }
    localStorage.setItem('ROBINGOOD_NEW_TAB', JSON.stringify(modifiedState));
    console.log({ modifiedState });
    setState(modifiedState);
  };

  console.log({ state });
  return (
    <Provider colorScheme="light" theme={lightTheme}>
      <>
        <HeaderComponent add={addNewItem}></HeaderComponent>
        <ResponsiveGridLayout
          draggableHandle=".drag-handle"
          verticalCompact={false}
          compactType={false}
          className="layout"
          cols={16}
          rowHeight={27}
          width={window.innerWidth - 5}
          onLayoutChange={(layout) => {
            modifyOrientation(layout);
          }}
        >
          {state.map((item, index) => {
            const orientation = {
              x: item.x,
              y: item.y,
              w: item.w,
              h: item.h,
              minW: item.minW,
              minH: item.minH,
            };
            return (
              <div
                key={item.key}
                data-grid={{
                  ...orientation,
                }}
              >
                <Card
                  heading={item.heading}
                  id={item.itemId}
                  type={item.itemType}
                  bodyPadding={hasBodyPadding(item.itemType)}
                  deleteItem={deleteItem}
                >
                  {renderItem(item.itemType, item.itemId, item.url, item.loadDefault)}
                </Card>
              </div>
            );
          })}
        </ResponsiveGridLayout>
        <div className={state.length<=4?"bottom-bar":""}>
          <Footer></Footer>
        </div>
      </>
    </Provider>
  );
};

export default App;
