import React, { useEffect, useRef, useState } from 'react';
import './Heading.scss';
import { Tooltip } from 'react-tippy';
import Hotkeys from 'react-hot-keys';

import {
  ActionButton,
  DialogTrigger,
  Button,
  ButtonGroup,
  Checkbox,
  Content,
  Divider,
  Flex,
  Footer,
  Form,
  Header,
  Heading,
  Text,
  TextField,
} from '@adobe/react-spectrum';
import { Dialog } from '@react-spectrum/dialog';
import { Link } from '../bookmark-link/Link';
import ContentEditable from 'react-contenteditable';
import ReactTooltip from 'react-tooltip';
import useDarkMode from 'use-dark-mode';

import NoteIcon from '../note_2.svg';
import LinkIcon from '../link_1.svg';
import AddIcon from '../more_1_full_black.svg';
import ListIcon from '../bullet.svg';
import RSSIcon from '../rss_new.svg';
import { Menu, MenuTrigger } from '@react-spectrum/menu';
import { Item } from '@react-stately/collections';

export const HeaderComponent = ({ add }) => {
  const ITEM_TYPES = {
    NOTES: 'notes',
    RSS: 'rss',
    TODO: 'todo',
    BOOKMARK: 'bookmark',
  };

  const darkMode = useDarkMode(false);
  console.log({darkMode})
  const [isShown, setShown] = useState(false);
  let [url, setUrl] = React.useState('');
  let [title, setTitle] = React.useState('');
  const text = useRef('Robin Good’s Personal Info');

  useEffect(() => {
    const title = localStorage.getItem('ROBIN_HEADING') || 'Robin Good’s Personal Info';
    text.current = title;
  }, []);

  function allStorage() {

    var values = [],
      keys = Object.keys(localStorage),
      i = keys.length;

    while ( i-- ) {
      values.push( {[keys[i]]:localStorage.getItem(keys[i]) });
    }

    return values;
  }

  const saveItems=(key)=>{
    console.log({key});
    if(key=="save") {
      const details = allStorage();
      var data = "text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(details));

      var a = document.createElement('a');
      a.href = 'data:' + data;
      a.download = 'robin_new_tab.json';
      a.innerHTML = 'download JSON';

      var container = document.getElementById('invisible');
      container.appendChild(a);
      a.click();
    }else if(key=="load"){
      console.log({inputFileRef});
      inputFileRef.current.click();
    }
    else if(key=="toggle"){
      darkMode.toggle();
    }
  }

  const handleChange = (evt) => {
    localStorage.setItem('ROBIN_HEADING', evt.target.value);
    text.current = evt.target.value;
  };

  const handleBlur = () => {
    localStorage.setItem('ROBIN_HEADING', text.current);
  };

  const inputFileRef = useRef( null );

  const onFilechange = ( e ) => {
    /*Selected files data can be collected here.*/
    console.log(e.target.files[0])
    const files=e.target.files[0];
    console.log(files);

    const fileReader = new FileReader();
    fileReader.readAsText(e.target.files[0], "UTF-8");
    fileReader.onload = e => {
      console.log(JSON.parse(e.target.result));
      localStorage.clear();

      let items=JSON.parse(e.target.result);
      items.forEach(item=>{
        const key=Object.keys(item)[0];
        const value=item[key]
        localStorage.setItem(key,value)
      })

      window.location.reload()

    };
  }

  const handleBtnClick = () => {
    /*Collecting node-element and performing click*/
    inputFileRef.current.click();
  }



  return (
    <div className="header-wrapper">

      <Hotkeys 
        keyName="shift+a" 
        onKeyDown={() => add(ITEM_TYPES.NOTES)}
        //onKeyUp={() => add(ITEM_TYPES.NOTES)}
      ></Hotkeys>
      <Hotkeys 
        keyName="shift+s" 
        onKeyDown={() => add(ITEM_TYPES.BOOKMARK)}
        //onKeyUp={() => add(ITEM_TYPES.NOTES)}
      ></Hotkeys>
      <Hotkeys 
        keyName="shift+d" 
        onKeyDown={() => add(ITEM_TYPES.TODO)}
        //onKeyUp={() => add(ITEM_TYPES.NOTES)}
      ></Hotkeys>
      <Hotkeys 
        keyName="shift+f" 
        onKeyDown={() => add(ITEM_TYPES.NOTES)}
        //onKeyUp={() => add(ITEM_TYPES.NOTES)}
      ></Hotkeys>

    <div className="absolute setting-icon extra-menu">
          <MenuTrigger>
            <ActionButton><svg xmlns="http://www.w3.org/2000/svg" height="18" viewBox="0 0 18 18" width="18">
              <title>More</title>
              <rect id="ToDelete" fill="#ff13dc" opacity="0" width="18" height="18" /><path d="M9,7.1A1.9,1.9,0,1,1,7.1,9,1.9,1.9,0,0,1,9,7.1Zm6,0A1.9,1.9,0,1,1,13.1,9,1.9,1.9,0,0,1,15,7.1ZM3,7.1A1.9,1.9,0,1,1,1.1,9,1.9,1.9,0,0,1,3,7.1Z" />
            </svg></ActionButton>
            <Menu onAction={(key) => saveItems(key)}>
              <Item key="save">Save</Item>
              <Item key="load">Load</Item>
              <Item key="toggle">Dark/Light</Item>
            </Menu>
          </MenuTrigger>
    </div>

      <input
        type="file"
        style={{display:"none"}}
        ref={inputFileRef}
        accept="json/*"
        onChange={onFilechange}
      />

      <div className="heading">

        <ContentEditable
          className="no-outline"
          html={text.current}
          onBlur={handleBlur}
          onChange={handleChange}
        />

      </div>

      <div
        className="dotted-bg d-flex centered"
        onMouseEnter={() => setShown(true)}
        onMouseLeave={() => setShown(false)}
      >

        {isShown ? (
          <div className="menu-items">
            {/*<ReactTooltip />*/}

            <Tooltip
              // options
              title="Notes"
              position="bottom"
              trigger="mouseenter"
              sticky="true"
              offset="20"
            >
            <img
              src={NoteIcon}
              className="menu-icon notes-icon-big pointer w-menu-icn"
              onClick={() => {
                add(ITEM_TYPES.NOTES);
              }} />

            </Tooltip>

            <Tooltip
              // options
              title="Lists"
              position="bottom"
              trigger="mouseenter"
              sticky="true"
            >
            <img
              src={ListIcon}
              className="menu-icon list-big-icon pointer w-menu-icn"
              onClick={() => {
                add(ITEM_TYPES.TODO);
              }}
            />
            </Tooltip>

            <Tooltip
              // options
              title="Links"
              position="bottom"
              trigger="mouseenter"
              sticky="true"
            >
            <img
              src={LinkIcon}
              className="menu-icon links-big-icon pointer w-menu-icn"
              onClick={() => {
                add(ITEM_TYPES.BOOKMARK);
              }}
            />
            </Tooltip>

            <DialogTrigger >
              <ActionButton UNSAFE_className="normal-btn">

                <Tooltip
                  // options
                  title="RSS"
                  position="bottom"
                  trigger="mouseenter"
                  sticky="true"
                >
                <img
                  src={RSSIcon}
                  className="menu-icon rss-big-icon pointer w-menu-icn"
                />
                </Tooltip>
              </ActionButton>
              {(close) => (
                <Dialog>
                  <Heading>
                    <Flex alignItems="center" gap="size-100">
                      <Text>Enter Feed URL</Text>
                    </Flex>
                  </Heading>

                  <Divider />
                  <Content>
                    <Form>
                      <TextField
                        label="Enter Title"
                        placeholder="Title"
                        autoFocus
                        value={title}
                        onChange={setTitle}
                      />
                      <TextField label="Feed URL" placeholder="URL" value={url} onChange={setUrl} />
                    </Form>
                  </Content>
                  <ButtonGroup>
                    <Button variant="secondary" onPress={() => {  setShown(false)
                      close()}}>
                      Cancel
                    </Button>
                    <Button
                      variant="cta"
                      onPress={() => {
                        if (url) {
                          add(ITEM_TYPES.RSS, title, url);
                          setShown(false)
                          close();
                        }
                      }}
                    >
                      Add
                    </Button>
                  </ButtonGroup>
                </Dialog>
              )}
            </DialogTrigger>
          </div>
        ) : <img src={AddIcon} className="fa-icon w-30 pointer" />}

      </div>
      </div>
  );
};
