import React, { useEffect, useState } from 'react';
import { faCheck, faExclamation, faHandRock, faTrash } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { List as ListItems, arrayMove } from 'react-movable';
import NewField from '../add-new/NewField';
import './List.scss';
import Mover from '../mover.svg';
import ImportantIcon from '../important.svg';
import ImportantDarkIcon from '../importance-dark.svg';
import StrikeThroughDark from '../strike-white.svg';
import StrikeThrough from '../strike.svg';
import useDarkMode from "use-dark-mode";

const colorMappings = ['#FFFFFF', '#efefef', '#999999','#434343'];
const textMappings = ['#6e6e6e','#131313', '#eeeeee', '#f7f7f7'];


export const List = ({ itemId }) => {
  const [list, setList] = useState([]);
  const darkMode = useDarkMode(false);

  const add = (value) => {
    modifyState([...list, { name: value, shade: 2, strike: false }]);
  };

  useEffect(() => {
    let modifiedId;
    if (itemId.startsWith('ROBIN_')) {
      modifiedId = itemId;
    } else {
      modifiedId = 'ROBIN_' + itemId;
    }
    const savedItem = localStorage.getItem(modifiedId) || '[]';
    setList(JSON.parse(savedItem));
  }, []);

  const onTextChange = (e) => {
    const value = e.target.value;
    const name = e.target.name;

    let lists = [...list];
    lists[name] = { ...list[name], name: value };
    modifyState(lists);
  };

  const modifyState = (lists) => {
    console.log('Modifying State');
    console.log({ lists });
    if (itemId.startsWith('ROBIN_')) {
      localStorage.setItem(itemId, JSON.stringify(lists));
    } else {
      localStorage.setItem('ROBIN_' + itemId, JSON.stringify(lists));
    }
    setList(lists);
  };

  const changeBackground = (index) => {
    let lists = [...list];

    const currentShade = lists[index].shade;
    lists[index] = { ...list[index], shade: (currentShade + 1) % 4 };
    modifyState(lists);
  };

  const deleteItem = (index) => {
    let lists = [...list];
    lists.splice(index, 1);
    modifyState(lists);
  };

  const strike = (index) => {
    let lists = [...list];
    list[index].strike = !list[index].strike;
    modifyState(lists);
  };

  return (
    <div className="list-wrapper">
      <NewField add={add} placeholder={'Enter list item'} />

      <div className="list relative">
        <ListItems
          lockVertically
          values={list}
          onChange={({ oldIndex, newIndex }) => modifyState(arrayMove(list, oldIndex, newIndex))}
          renderList={({ children, props }) => <ul {...props}>{children}</ul>}
          renderItem={({ value, props }) => {
            const index = props.key;
            return (
              <li {...props} className="input-list-item">
                <div
                  className={`d-flex relative actions shade-${value.shade}`}
                  style={{
                    // background: colorMappings[value.shade],
                    // color: textMappings[value.shade],
                  }}
                >
                  <div className="pointer handle" data-movable-handle>
                    <img src={Mover} alt="React Logo" className="move-handler" />
                  </div>
                  {value.strike && (
                    <div className="strike-div relative">
                    <s>{value.name}</s>
                    </div>
                  )}
                  {!value.strike && (
                    <input
                      className={`list-item shade-color-${value.shade} ${value.shade===0?'bold-font':''}`}
                      value={value.name}
                      name={index}
                      onChange={onTextChange}
                      style={{
                        // color: textMappings[value.shade],
                      }}
                    />
                  )}

                  <div className="absolute action-items">

                    {
                      !darkMode && (<img src={ImportantDarkIcon} className="pointer important-icon-dark" onClick={() => changeBackground(index)}/>
                      )}
                    {
                      darkMode && (<img src={ImportantIcon} className="pointer important-icon-light" onClick={() => changeBackground(index)}/>
                      )}


                    {
                      !darkMode && (<img src={StrikeThroughDark} className="pointer strike-icon-light" onClick={() => strike(index)} />
                      )}
                    {
                      darkMode && (<img src={StrikeThrough} className="pointer strike-icon-dark" onClick={() => strike(index)} />
                      )}

                    {/*<FontAwesomeIcon*/}
                    {/*  icon={faExclamation}*/}
                    {/*  className="fa-icon importance pointer movable-icon"*/}
                    {/*  onClick={() => changeBackground(index)}*/}
                    {/*/>*/}
                    {/*<FontAwesomeIcon*/}
                    {/*  icon={faCheck}*/}
                    {/*  className="fa-icon check-icon pointer"*/}
                    {/*  onClick={() => strike(index)}*/}
                    {/*/>*/}
                    <FontAwesomeIcon
                      icon={faTrash}
                      className="delete-icon pointer"
                      onClick={() => deleteItem(index)}
                    />
                  </div>
                </div>
              </li>
            );
          }}
        />
      </div>
    </div>
  );
};
