import React, { useEffect, useState } from 'react';
import * as rssParser from 'rss-parser';
import RSSLink from '../rss-link/RSS-Link';

import { faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import RSSFirst from '../rss-first/RSSFirst';
import './RSS.scss';
import { BACKEND_URL } from '../../constants';

const pageSize = 10;

const RSS = ({ id, url: feedUrl }) => {
  const [rssLinks, setRssLinks] = useState([]);
  const [currentLinks, setCurrentLinks] = useState([]);

  const [page, setPage] = useState(0);
  const [totalSize, setTotalSize] = useState(0);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    parseContents();
  }, []);

  const parseContents = async () => {
    setLoading(true);
    let parser = new rssParser();
    const CORS_PROXY = `${BACKEND_URL}/rss/`;
    console.log({ feedUrl });
    const url = encodeURIComponent(feedUrl);
    let feed = await parser.parseURL(CORS_PROXY + url);

    console.log(feed.items.length);
    console.log(pageSize);

    setTotalSize(Math.ceil(feed.items.length / pageSize));
    console.log(totalSize);
    setRssLinks(feed.items);
    setCurrentLinks(feed.items.slice(0, 10));
    setLoading(false);
  };

  return (
    <>
      <div className="main-panel">
        {currentLinks.map((item, index) => {
          if (index === 0) {
            return <RSSFirst {...item} key={'RSS_' + item.link} />;
          } else {
            return <RSSLink {...item} key={'RSS_' + item.link} />;
          }
        })}
      </div>
      {!loading && (
        <div className="pagination">
          <div>
            <FontAwesomeIcon
              icon={faAngleLeft}
              className="fa-icon importance pointer"
              onClick={() => {
                console.log(page);
                console.log(totalSize);
                if (page - 1 <= totalSize && page > 0) {
                  const newPage = page - 1;
                  setPage(newPage);
                  setCurrentLinks(
                    rssLinks.slice(newPage * pageSize, newPage * pageSize + pageSize),
                  );
                }
              }}
            />
          </div>
          <div className="page">{page + 1 + ' / ' + totalSize}</div>
          <div>
            <FontAwesomeIcon
              icon={faAngleRight}
              className="fa-icon importance pointer"
              onClick={() => {
                if (page + 1 < totalSize && page >= 0) {
                  const newPage = page + 1;
                  setPage(newPage);
                  setCurrentLinks(
                    rssLinks.slice(newPage * pageSize, newPage * pageSize + pageSize),
                  );
                }
              }}
            />
          </div>
        </div>
      )}
    </>
  );
};

export default RSS;
