import React, { useEffect, useRef, useState } from 'react';
import './Card.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faTrash } from '@fortawesome/free-solid-svg-icons';
import ContentEditable from 'react-contenteditable';

import NoteIcon from '../note.svg';
import LinkIcon from '../link.svg';
import AddIcon from '../plus.svg';
import ListIcon from '../list.svg';
import RSSIcon from '../rss.svg';
import { Link } from '../bookmark-link/Link';

const ITEM_TYPES = {
  NOTES: 'notes',
  RSS: 'rss',
  TODO: 'todo',
  BOOKMARK: 'bookmark',
};

const getIcon = (type) => {
  switch (type) {
    case ITEM_TYPES.NOTES:
      return <img src={NoteIcon} className="notes-icon menu-icon op-icon pointer w-30" />;
    case ITEM_TYPES.RSS:
      return <img src={RSSIcon} className="rss-icon menu-icon op-icon pointer w-30" />;
    case ITEM_TYPES.TODO:
      return <img src={ListIcon} className="list-icon menu-icon op-icon pointer w-30" />;
    case ITEM_TYPES.BOOKMARK:
      return <img src={LinkIcon} className="link-icon menu-icon op-icon pointer w-30" />;
  }
};

const Card = ({ heading, children, type, options, bodyPadding = true, deleteItem, id }) => {
  console.log(bodyPadding);
  const [isShown, setShown] = useState(false);

  const text = useRef(heading);

  useEffect(() => {
    const title = localStorage.getItem(`ROBIN_${id}_HEADING`) || heading;
    text.current = title;
  }, []);

  const handleChange = (evt) => {
    localStorage.setItem(`ROBIN_${id}_HEADING`, evt.target.value);
    text.current = evt.target.value;
  };

  const handleBlur = () => {
    localStorage.setItem(`ROBIN_${id}_HEADING`, text.current);
  };

  return (
    <div
      className="card card-wrapper"
      onMouseEnter={() => setShown(true)}
      onMouseLeave={() => setShown(false)}
    >
      {isShown && (
        <div className="delete-icon pointer">
          <FontAwesomeIcon
            icon={faTrash}
            className="fa-icon pointer"
            onClick={() => {
              deleteItem(id);
            }}
          />
        </div>
      )}
      <div style={{ overflow: 'visible', height: '0px', width: '0px' }}>
        <div className="card-container">
          <div className="drag-handle card-heading">
            <div className="d-flex ml-9">
              {getIcon(type)}
              <ContentEditable
                className='no-outline text-field' style={{ marginleft: type === ITEM_TYPES.NOTES ? '-2px' : '0px' }}
                html={text.current}
                onBlur={handleBlur}
                onChange={handleChange}
              />
            </div>
            <div className="options">{options}</div>
          </div>

          <div className="card-content" style={{ padding: bodyPadding ? '8px' : '0px' }}>
            <div className="card-height-helper">
              <div className="panel-height-helper baron baron__root baron__clipper panel-content--scrollable _scrollbar">
                {children}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Card;
